  text-decoration: none; }

/*PAGE SINGLE*/
.cover-single {
  max-height: 300px;
  overflow: hidden; }

button[name="update_cart"] {
  display: none !important; }

.onsale {
  background: #00bdaa !important;
  color: #2b0055 !important;
  text-transform: uppercase;
  padding: 0.75rem 0.5rem !important; }

footer h3 {
  border-bottom: solid 1px;
  font-weight: 700; }

footer li {
  margin-bottom: 1rem; }

footer img {
  max-width: 30px; }

footer a:hover {
  color: #ff0080 !important; }

footer .block-bottom p {
  color: #ff0080; }

#wrapper-footer-full {
  background: #2d247b;
  color: #f1f1f1; }

.woocommerce-MyAccount-navigation .list-group-item {
  display: block;
  margin: 0.5rem auto;
  padding: 0.5rem;
  border: solid 1px;
  border-radius: 10px;
  text-align: center;
  max-width: 180px; }

@media screen and (max-width: 768px) {
  .woocommerce-MyAccount-navigation .list-group-item {
    display: inline-block; } }

a {
  text-decoration: none; }

.box-shadow,
.box-article,
.request img {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.05); }

.box-article {
  border: solid 1px #f1f1f1; }

.border-radius {
  border-radius: 0.5rem; }

.icons-xl {
  font-size: 50px; }

.icon-expand-more {
  position: absolute;
  margin-top: 5px; }

ul {
  padding: 0; }

.text-sm {
  font-size: 17px; }

.text-md,
h3 {
  font-size: 25px; }

.text-lg,
h2 {
  font-size: 30px; }

.text-xl,
h1 {
  font-size: 45px; }

.text-bold {
  font-size: bold; }

.text-dark {
  color: #2b0055; }

.transition {
  transition: all ease-in-out 0.3s; }

.bg-white {
  background-color: white; }

.bg-gray {
  background-color: #f1f1f1; }

.bg-dark {
  background-color: #2b0055; }

.bg-accent {
  background-color: #00bdaa; }

.categories img {
  max-height: 150px;
  width: auto; }

/*NOTE: WP Main(#main-menu ) menu hidden till the page is loades(watch the js file)*/
.main-layer-style {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  max-width: 1500px;
  height: 100vh;
  z-index: 10; }

.menu-container ul li {
  background: white; }

.menu-container ul li ul li {
  display: block;
  margin: 0px; }

.menu-container {
  background: #1b1b1b; }

.menu-container ul {
  float: right;
  list-style: none;
  position: relative;
  width: 100%; }

.menu-container ul li {
  display: inline-block;
  margin: 0 5px; }

/* .menu-container ul li:hover > ul{
			display: block;
		} */
.menu-container ul li a {
  text-decoration: none;
  padding: 20px 15px;
  color: rgba(0, 0, 0, 0.75); }

.menu-container ul li a:hover {
  color: #ff0080; }

.menu-container ul ul li a:hover {
  box-shadow: none; }

.menu-container ul ul {
  position: absolute;
  border-top: 3px solid #ff0080;
  display: none;
  margin-top: 10px; }

.menu-container ul ul ul {
  border-top: none; }

.menu-container ul ul li {
  position: relative;
  margin: 0px;
  width: 250px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); }

.menu-container ul ul li a {
  display: block; }

.menu-container ul ul ul li {
  position: relative;
  top: -64px;
  left: 250px; }

@media screen and (min-width: 1125px) {
  .menu-container ul li:hover > ul {
    display: block; } }

@media screen and (max-width: 1124px) {
  .icon-menu-container,
  .icon-line,
  .menu-container .dropdown-toggle,
  .menu-container ul,
  .menu-container li {
    display: block; }
  header {
    height: 90px; }
  .header-cart-detail {
    left: -150px; }
  .splide__arrow {
    bottom: -15px !important;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15); }
  .icon-line,
  .menu-container {
    transition: all ease-in-out 0.3s; }
  .icon-menu {
    width: 35px;
    height: 35px;
    transform: rotate(-180deg);
    scale: 1;
    margin-left: 1rem; }
  .icon-menu:hover {
    cursor: pointer; }
  .icon-menu {
    width: 35px;
    height: 35px;
    transition: all ease-in-out 0.3s;
    margin-left: 1rem; }
  .icon-menu:hover {
    transition: all ease-in-out 0.3s; }
  .icon-line {
    width: 35px;
    height: 4px;
    background: #ff0080;
    position: absolute;
    border-radius: 2px;
    transition: all ease-in-out 0.3s; }
  .icon-line:nth-child(2) {
    margin-top: 12px;
    width: 35px; }
  .icon-line:nth-child(3) {
    margin-top: 25px;
    width: 35px; }
  .menu-container {
    margin-top: 20px; }
  .menu-container, .main-menu {
    margin-top: 20px; }
  .main-menu {
    padding: 30px; }
  .menu-container ul li {
    margin: 0; }
  .menu-container ul li {
    width: 100%; }
  .menu-container ul ul ul li {
    left: 0px;
    top: 0px; }
  .menu-container .dropdown-toggle {
    min-width: 220px; }
  .menu-container ul {
    float: none; }
  .menu-container .dropdown-toggle,
  .menu-container ul,
  .menu-container li {
    position: relative !important; }
  .menu-container ul li a {
    padding: 10px 20px;
    display: block; }
  .menu-container {
    position: absolute !important;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.2s linear; }
  .menu-container, .main-menu {
    margin-top: 20px; }
  .main-menu {
    background: white;
    border-radius: 0 0 0.5rem 0;
    max-width: 300px;
    position: relative !important;
    max-height: 80vh;
    overflow-y: scroll; }
  header .dgwt-wcas-search-wrapp {
    max-width: 50px; }
  /*Estilos js*/
  .rotate-1 {
    transform: rotate(45deg);
    margin-top: 12px;
    background: #2b0055;
    width: 30px !important; }
  .opacity-line-0 {
    width: 0 !important;
    opacity: 0; }
  .rotate-2 {
    transform: rotate(-45deg);
    margin-top: 12px !important;
    background: #2b0055;
    width: 30px !important; }
  .d-menu {
    visibility: visible;
    opacity: 1; }
  iframe {
    max-height: 480px; }
  .thumb-container-single {
    height: 200px; } }

@media screen and (max-width: 480px) {
  .menu-container {
    width: 100vw;
    margin-top: 0; }
  .border-radius {
    border-radius: 0; }
  .btn {
    letter-spacing: 1px;
    padding: 0.375rem 1.25rem; }
  .text-sm {
    font-size: 18px; }
  .text-lg {
    font-size: 28px; }
  .text-xl {
    font-size: 35px; }
  header {
    height: 80px; }
  .request img {
    display: none; }
  .thumb-container {
    height: 120px;
    margin-bottom: 1.25rem; }
  .box-article {
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    border: 0;
    border-bottom: solid 1px #f1f1f1; }
  #wrapper-footer-full .textwidget {
    text-align: center; } }
